
import { defineComponent, computed } from "vue";
import { footerWidthFluid } from "@/core/helpers/config";

export default defineComponent({
  name: "KTFooter",
  setup() {
    const getCurrentYear = computed(() => {
      return new Date().getFullYear();
    });
    return {
      footerWidthFluid,
      getCurrentYear,
    };
  },
});
